import styled from 'styled-components'
import { MEDIA_QUERY, FONT_SIZE } from '../../../constants/style'
import { PageWidth } from '../../../components/general'


const Container = styled.div`
  margin: 60px auto 60px;
  padding: 0 8px;
  width: 90%;
  ${MEDIA_QUERY.desktop} {
    width: 80%;
  }
`

const Title = styled.div`
  margin-top: 40px;
  font-size: ${FONT_SIZE.lg};
  font-weight: bold;
  ${MEDIA_QUERY.tablet} {
    margin-top: 60px;
    font-size: ${FONT_SIZE.xl};
  }
  ${MEDIA_QUERY.desktop} {
    margin-top: 80px;
    font-size: ${FONT_SIZE.xxl};
  }
`

const Content = styled.div`
  width: 100%;
  text-align: left;
  h3 {
    margin: 48px 0 12px;
    font-size: ${FONT_SIZE.lg};
    ${MEDIA_QUERY.tablet} {
      font-size: ${FONT_SIZE.lg};
    }
    ${MEDIA_QUERY.desktop} {
      font-size: ${FONT_SIZE.xl};
    }
  }

  p {
    margin: 0;
    ${MEDIA_QUERY.tablet} {
      font-size: ${FONT_SIZE.md};
    }
    ${MEDIA_QUERY.desktop} {
      font-size: ${FONT_SIZE.md};
    }
  }

  p + p {
    margin-top: 8px;
  }
`

export default function Notice() {
  return (
    <PageWidth>
      <Container>
        <Title></Title>
        <Content>
          <h3></h3>
          <p></p>
          <p> FTC   </p>
          <p> </p>
          <p></p>

          <h3></h3>
          <p></p>
          <p>...</p>
          <p></p>
        </Content>
      </Container>
    </PageWidth>
  )
}
