import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { MEDIA_QUERY } from '../constants/style'

//  Route 
export const PageHeight = styled.div`
  position: relative;
  margin-top: 49px;
  width: 100%;
  flex: 1;
  ${MEDIA_QUERY.desktop} {
    margin-top: 121px;
  }
`

export const AdminPageHeight = styled.div``

//  PageWidth, AdminPageWidth 
export const PageWidth = styled.div`
  width: 90%;
  margin: 0 auto;
  text-align: center;
  ${MEDIA_QUERY.desktop} {
    max-width: 1200px;
  }
`
export const AdminPageWidth = styled.div``

//  FullWidth 
export const FullWidth = styled.div`
  width: 100%;
  text-align: center;
`

//  componentheight 
export const ImgAnchor = styled(Link)`
  display: inline-block;
  width: 100%;
  color: transparent;
  &:hover {
    color: transparent;
  }
`
