import { FullModal } from '../../components/Modal'

export function AddCartModal({ isModalOpen, handleModalClose }) {
  return (
    <FullModal
      open={isModalOpen}
      content=' ! '
      onClose={handleModalClose}
    />
  )
}

export function SoldOutCartModal({ isModalOpen, handleModalClose }) {
  return (
    <FullModal
      open={isModalOpen}
      content=''
      onClose={handleModalClose}
    />
  )
}
