import { useState } from 'react'
import { Link, useParams, useHistory } from 'react-router-dom'
import { PageWidth } from '../../../components/general'
import {
  Title,
  Steps,
  TitleWidth,
  TitleGroup,
  CartTitles,
  CartTitle,
  TotalPrice,
  BtnFlexCenter,
  DeliveryData
} from '../../../components/checkoutSystem/Step'
import { GeneralBtn } from '../../../components/Button'
import { OrderItem } from '../../../components/checkoutSystem/step3OrderItem'
import { getOrderOne } from '../../../webAPI/orderAPI'

export default function Step3() {
  const location = useHistory()
  let { ticket } = useParams()
  const [orderData, setOrderData] = useState(() => {
    ;(async () => {
      const result = await getOrderOne(ticket)
      if (result.ok === 0) {
        console.log(result.message)
        return location.push('/')
      }
      return setOrderData(result.data)
    })()
  })
  return (
    <PageWidth>
      <Steps />
      {orderData && (
        <>
          <Title></Title>
          <DeliveryData>: {orderData.Member.username}</DeliveryData>
          <DeliveryData>: {orderData.orderEmail}</DeliveryData>
          <DeliveryData>: {orderData.orderName}</DeliveryData>
          <DeliveryData>: {orderData.orderAddress}</DeliveryData>
          <DeliveryData>: {orderData.orderPhone}</DeliveryData>

          <Title></Title>
          <DeliveryData>{orderData.payment}</DeliveryData>
          <Title></Title>
          <DeliveryData>{orderData.shipping}</DeliveryData>

          <Title></Title>
          <CartTitles>
            <TitleWidth />
            <TitleGroup>
              <CartTitle></CartTitle>
              <CartTitle></CartTitle>
              <CartTitle></CartTitle>
            </TitleGroup>
          </CartTitles>
          {orderData.Order_items.map((item) => (
            <OrderItem key={item.productId} item={item} />
          ))}
          <TotalPrice>{` NT$${orderData.subTotal - 80}`}</TotalPrice>
          <TotalPrice> NT$80</TotalPrice>
          <TotalPrice>{` NT$ ${orderData.subTotal}`}</TotalPrice>
        </>
      )}
      <Link to='/'>
        <BtnFlexCenter>
          <GeneralBtn
            color='primary'
            children=''
            buttonStyle={{ marginTop: '20px' }}
          />
        </BtnFlexCenter>
      </Link>
    </PageWidth>
  )
}
